  ts_24: `Electronic Communications`,
  ts_24_1: `Such actions as for example accessing the Application or sending emails to Motipio constitutes
  electronic communications. Users consent to receive electronic communications and Users agree
  that Motipio‚Äôs provision all such communications shall be made electronically to satisfy any legal
  requirement that such communications be in writing.`,
  ts_25: `Additional Terms and Conditions`,
  ts_25_1: `No joint venture, partnership, employment, or agency relationship exists between Users and
  Motipio as a result of these Terms and Conditions or use of the Application.`,
  ts_25_2: `Motipio's performance hereunder is subject to existing laws and legal process, and nothing
  contained in these Terms and Conditions is in derogation of Motipio's right to comply with
  governmental, court and law enforcement requests or requirements relating to Users use of the
  Application or information provided to or gathered by Motipio with respect to such use.`,
  ts_25_3: `If any court having competent jurisdiction holds any provision of this Terms and Conditions
  invalid or unenforceable in any respect, such provision shall be enforced to the maximum extent
  permitted by law, and the remaining provisions of this Terms and Conditions shall continue in full
  force and effect.`,
  ts_25_4: `The failure or delay of either party to exercise or enforce any right or claim does not constitute a
  waiver of such right or claim and shall in no way affect that party‚Äôs right to later enforce or
  exercise it, unless such party issues an express written waiver, signed by a duly authorized
  representative.`,
  ts_25_5: `Unless otherwise specified herein, these Terms and Conditions constitutes the entire Agreement
  between the user and Motipio with respect to the Application and Services and supersedes all prior or
  contemporaneous communications and proposals, whether electronic, oral or written, between the
  User and Motipio with respect to the Application. A printed version of these Terms and Conditions
  and of any notice given in electronic form shall be admissible in judicial or administrative
  proceedings based upon or relating to these Terms and Conditions to the same extent and subject
  to the same conditions as other business documents and records originally generated and
  maintained in printed form. It is the express wish to the parties that these Terms and Conditions
  and all related documents be written in English.`,
