	faq_a_13: `–í: –Ø–∫—â–æ –≤–∏ –∑–∞–ø–∏—Ç–∞–ª–∏ –ü–†–û –∑ —è–∫–∏–º –æ–±–≥–æ–≤–æ—Ä—é–≤–∞–ª–∏ –∑–∞–ø–∏—Ç, –ø—ñ–¥—Ç–≤–µ—Ä–¥–∏—Ç—å —Ü–µ - –≤–∏ –º–æ–∂–µ—Ç–µ –¥—ñ–ª–∏—Ç–∏—Å—å —Ü–∏–º, —è–∫—â–æ –∂ –ü–†–û –Ω–µ –¥–∞—î –∑–≥–æ–¥–∏ –Ω–∞ —Ü–µ, –Ω–∞ –≤–∞—Å –º–æ–∂—É—Ç—å –Ω–∞–∫–ª–∞–¥–∞—Ç–∏—Å—å —à—Ç—Ä–∞—Ñ–Ω—ñ —Å–∞–Ω–∫—Ü—ñ—ó. –ö–æ–º–∞–Ω–¥–∞ Motipio —Ä–∞–¥–∏—Ç—å –≤–∞–º, –æ–±–≥–æ–≤–æ—Ä–∏—Ç–∏ —Ü–µ –ø–∏—Ç–∞–Ω–Ω—è –∑ –ü–†–û –±–µ–∑–ø–æ—Å–µ—Ä–µ–¥–Ω—å–æ –≤ –º–æ–º–µ–Ω—Ç –æ–±–≥–æ–≤–æ—Ä–µ–Ω–Ω—è –∑–∞–ø–∏—Ç—É.`,
	faq_q_14: `–ü: –°–∫—ñ–ª—å–∫–∏ —Å–∏–º–≤–æ–ª—ñ–≤ —è –º–æ–∂—É –Ω–∞–ø–∏—Å–∞—Ç–∏ –≤ –∑–∞–ø–∏—Ç—ñ?`,
