	faq_q_15: `–ü: –Ø–∫—â–æ —è –Ω–µ –∑–∞–¥–æ–≤–æ–ª–µ–Ω–∏–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–º —è–∫–∏–π –æ—Ç—Ä–∏–º–∞–≤ –ø—ñ—Å–ª—è –ó–∞–≤–µ—Ä—à–µ–Ω–Ω—è –∑–∞–ø–∏—Ç–∞?`,
