const listAnchorLinks = document.querySelectorAll("[href*='#']");
const listSection = [...document.querySelectorAll("section[id]")];
const dots = document.querySelector(".dots");
let dotsLinks = [];
if(dots){
  dotsLinks = [...dots.querySelectorAll("span")];
}

let animating = false;
const duration = 1200;

listAnchorLinks.forEach(el =>
  el.addEventListener('click', e => {
    e.preventDefault();
    const targetId = el.hash;
    const index = listSection.findIndex(link => link.id === targetId.slice(1));
    if(dots)changeCurrDot(index);
    
    const targetPositon = document.querySelector(targetId).offsetTop;
    smoothScrol(targetPositon + 5, duration);
    animating = true;
    setTimeout(() => (animating = false), duration);
  })
);

function changeCurrDot (index, links = dotsLinks) {
  links.forEach((link, item) => {
    if(item === index) {
      link.classList.add("dot_check");
    }else {
      link.classList.remove("dot_check");
    }
  })
}


function smoothScrol(targetPositon, duration) {
  const startPosition = window.pageYOffset;
  const distance = targetPositon - startPosition;
  let start = null;
  window.requestAnimationFrame(step);

  function step(timestamp) {
    if (!start) start = timestamp;
    const progress = timestamp - start;
    window.scrollTo(
      0,
      easeInOutCubic(progress, startPosition, distance, duration)
    );
    if (progress < duration) window.requestAnimationFrame(step);
  }
}

function easeInOutCubic(t, b, c, d) {
  t /= d / 2;
  if (t < 1) return (c / 2) * t * t * t + b;
  t -= 2;
  return (c / 2) * (t * t * t + 2) + b;
}

const home = document.querySelector('#home');

if (home && screen.width > 991) {
  document.querySelector('html').style.overflow = 'hidden';
  const sections = [...document.querySelectorAll('section')];
  const reverseSections = [...sections].reverse();

  document.addEventListener('DOMContentLoaded', () => {
    sections.forEach((section, item) => {
      const topCord = section.offsetTop - 3;
      const buttomCord = section.offsetTop + window.innerHeight;
  
      if(topCord <= pageYOffset && buttomCord > pageYOffset ) {
        currSection = section;
        dotsLinks[item].classList.add("dot_check");
      }
    })
  });
  
  home.addEventListener('wheel', e => {
    let targetSection = sections.find(
      section => section.offsetTop > window.pageYOffset
    );
    let sectionId = 0;
    if (animating) return;
    if (e.deltaY > 0) {
      targetSection = sections.find((section, item) => {
        sectionId = item;
        return section.offsetTop > window.pageYOffset;
      });
      changeCurrDot(sectionId, dotsLinks);
      if (targetSection) smoothScrol(targetSection.offsetTop + 4, duration);
    } else {
      targetSection = reverseSections.find((section, item) => {
        sectionId = item;
        return section.offsetTop + 5 < window.pageYOffset;
      });
      changeCurrDot(sectionId, [...dotsLinks].reverse());
      if (targetSection) smoothScrol(targetSection.offsetTop + 4, duration);
    }
    animating = true;
    setTimeout(() => (animating = false), duration);
    e.preventDefault();
  });
}



